body, html{
    margin: 0px;
    height: 100vh;
    width: 100vw;
    overflow-x: hidden;
}

.login-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 25px;
    background-color: black;
}

.parte-superior {
    background-color: #ee1515;
    color: white;
    height: 46.5vh;
    width: 100vw;
    text-align: center;
}

.texto-superior{
    margin-top: 100px;

}

.texto-inferior{
    margin-top: 150px;
}


.parte-central {
    position: absolute;
    z-index: 20;
    background-color: #222224 ;
    color: white;
    width: 100vw;
    height: 7vh;
    text-align: center;
}




.poke-button-outside{
    margin: auto;
    position: relative;
    z-index: 10;
    width: 120px;
    height: 120px;
    padding: 0px 5px;
    border: 20px solid #222224;
    background-color: white;
    border-radius: 70px;
    text-align: center;
    top: -35px;
}

.poke-button-center{
    position: relative;
    z-index: 11;
    width: 60px;
    height: 60px;
    padding: 0px 5px;
    border: 2px solid #222224;
    border-radius: 50px;
    text-align: center;
    margin: auto;
    top: 10px;
}

.poke-button-center:hover{
    border: 2px solid #222224;
    background-color: red;
}

.poke-button-center:focus{
    border: 2px solid #222224;
    box-shadow: none;
    outline: none;
}


.parte-inferior {
    background-color: #f0f0f0;
    color: black;
    width: 100vw;
    height: 53.5vh;
    text-align: center;
}


.logo{
    height: 75px;
    width: 150px;
}


.search-container{
    height: 100%;
    background: white;
}

.busqueda {
    display: flex;
    width: 100vh;
    align-items: center;
    justify-content: center;
}


.pokesearch{
    width: 80%;
    border-bottom: 2px solid black;
    box-shadow: 1px;
}

.pokesearch:focus {
    box-shadow: none;
    outline: none !important;
}


.pokesearch__button{
    margin: 30px 0px 30px;
}

.pokesearch__button:focus{
    box-shadow: none;
    outline: none !important;
}

.navbar{
    width: 100%;

}


.outButtonContainer{
    margin-right: 10px;
}

.outButton{
    border: 1px solid black;
    border-radius: 10px;
    background-color: #F5F8FA;
}

.outButton:hover{
    background-color:#f5f8facb ;
}



.pokescreen-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    background: #F5F8FA;

}

.pokeCard__background{
    border: 1px solid black;
}

.pokeCard__container{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
   
}



.pokeCard__pokemon{
    display: flex;
    flex-direction: column;
    height: auto;
    width: 330px;
    margin: 10px;
    padding: 20px;
}

.pokeCard__pokemon img {
    margin: auto;
    height: 200px;
    width: 100%;
}




.tipos{
    display: flex;
    justify-content: space-around;
    text-align: center;
}


.pokeCard__button{
    border-radius: 10px;
    height: 50px;
    margin-top: auto;
    border: 1px solid white;
}
