$urlElectrico: url( '../resources/fondos/trueno2.jpg' );
$urlVolador: url('../resources/fondos/sky.jpg');
$urlPlantaBicho: url('../resources/fondos/bosque.jpg');
$urlFuego: url('../resources/fondos/volcan.jpg');
$urlAgua: url('../resources/fondos/Agua2.jpg');
$urlPsiquico: url('../resources/fondos/galaxy.jpg');
$urlTierra: url('../resources/fondos/cueva.jpg');
$urlRoca: url('../resources/fondos/rocks.jpg');
$urlFantasma: url('../resources/fondos/fantasma.jpg');
$urlHielo: url('../resources/fondos/hielo.jpg');
$urlLucha: url('../resources/fondos/dojo2.jpg');
$urlDragon: url('../resources/fondos/dragon.jpg');
$urlNormal: url('../resources/fondos/normal.jpg');
$urlVeneno: url('../resources/fondos/poison.jpg');

@mixin tipo( $background, $background-image:none, $background-size:cover, $text-color:white ) {
    background-color: $background;
    background-image: $background-image;
    background-size: $background-size; 
    color: $text-color;
}

.Planta{
    @include tipo(green, $urlPlantaBicho)
}

.Fuego{
    @include tipo(red, $urlFuego)
}

.Agua{
    @include tipo( lightblue, $urlAgua)
}

.Normal{
    @include tipo(brown, $urlNormal)
}

.Eléctrico{
    @include tipo(yellow, $urlElectrico);
}

.Volador{
    @include tipo(skyblue, $urlVolador);
}

.Psíquico{
    @include tipo(pink, $urlPsiquico)
} 

.Bicho{
    @include tipo(lightgreen, $urlPlantaBicho )
}

.Tierra{
    @include tipo(brown, $urlTierra )
}

.Roca{
    @include tipo(darkred, $urlRoca)
}

.Hielo{
    @include tipo(lightskyblue, $urlHielo)
}

.Lucha{
    @include tipo(red, $urlLucha)
}

.Fantasma{
    @include tipo(purple, $urlFantasma)
}

.Veneno{
    @include tipo(purple, $urlVeneno)
}

.Dragon{
    @include tipo(purple, $urlDragon)
}
